import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Impressum from "@/views/Impressum";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },{
    path: '/impressum',
    name: 'Impressum',
    component: Impressum
  },
  {
    path: '/061121.html',
    name: 'FirstEvent',
    component: () => import('../views/FirstEvent.vue')
  },
  {
    path: '/131121.html',
    name: 'SecondEvent',
    component: () => import('../views/SecondEvent.vue')
  },
  {
    path: '/231121.html',
    name: 'ThirdEvent',
    component: () => import('../views/ThirdEvent.vue')
  },
  { path: '*', redirect: '/' }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
